import React, { useState } from "react";
import { postProducto } from "../../api/PostProducto";
import { useForm } from "../../hooks/useForm";
import Swal from "sweetalert2";
export const CreateProductScreen = () => {
  const [values, handleInputChange, reset] = useForm({
    modelo: "",
    Precio: "",
    PrecioAnterior: "",
    color: "",
    almacenamiento: "",
    imagen: "",
  });
  const {
    modelo,
    Precio,
    PrecioAnterior,
    color,
    almacenamiento,
    imagen,
  } = values;
  const [loading, setloading] = useState(false);
  const [img, setimg] = useState([]);
  const [image, setimage] = useState([]);

  const fileChangedHandler = (event) => {
    setimg({
      file: URL.createObjectURL(event.target.files[0]),
    });
    setimage({ selectedFile: event.target.files[0] });
    console.log(image);
  };

  const uploadHandler = () => {};

  const guardar = async (e) => {
    setloading(true);
    if (img.file) {
      console.log("entro aqui en imagen");
      const formData = new FormData();
      formData.append("avatar", image.selectedFile, image.selectedFile.name);
      formData.append("body", JSON.stringify({ ...values, no: true }));
      console.log(formData);
      e.preventDefault();
      postProducto(formData)
        .then((res) => {
          setimg([]);
          setimage([]);
          setloading(false);
          const { modelo } = res;
          Swal.fire(
            "¡Nuevo Producto Creado!",
            ` ${modelo} Creado en la base de datos`,
            "success"
          );
          reset();
        })
        .catch((error) => {
          setloading(false);
          Swal.fire("Eror al realizar el pedido", `${error}`, "error");
        });
    } else {
      console.log("entro en no imagen");
      const formData = new FormData();

      formData.append("body", JSON.stringify({ ...values, no: false }));
      console.log(formData);
      e.preventDefault();
      postProducto(formData)
        .then((res) => res.json())
        .then((res) => {
          setloading(false);
          const { modelo } = res;
          Swal.fire(
            "¡Nuevo Producto Creado!",
            ` ${modelo} Creado en la base de datos`,
            "success"
          );
          reset();
        })
        .catch((error) => {
          setloading(false);
          Swal.fire("Eror al realizar el pedido", `${error}`, "error");
        });
    }
  };
  return (
    <div className="container-fluid">
      <h1>Crear Producto</h1>
      <div className="row">
        <form
          encType="multipart/form-data"
          className="col-12"
          onSubmit={guardar}
        >
          <div class="form-row">
            <div className="col-12 col-md-6 form-group">
              <label htmlFor="usr">Nombre de Producto:</label>

              <input
                value={modelo}
                onChange={handleInputChange}
                name="modelo"
                type="text"
                className="form-control"
              />
            </div>

            <div className=" col-12 col-md-3 form-group">
              <label htmlFor="usr">Precio:</label>

              <input
                value={Precio}
                name="Precio"
                onChange={handleInputChange}
                type="number"
                className="form-control"
              />
            </div>

            <div className="col-12  col-md-3 form-group">
              <label htmlFor="usr">Precio Anterior:</label>
              <input
                name="PrecioAnterior"
                onChange={handleInputChange}
                value={PrecioAnterior}
                type="number"
                className="form-control"
              />
            </div>
            <div className="col-12 col-md-6 form-group">
              <label htmlFor="usr">Imagen:</label>
              <input
                className="pl-3 "
                name="avatar"
                type="file"
                onChange={fileChangedHandler}
              />
              <figure>
                <img width="120" src={img.file} />
              </figure>
            </div>
            <div className="col-12 col-md-3 form-group">
              <label htmlFor="usr">Color:</label>
              <input
                value={color}
                name="color"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>
            <div className="col-6 col-md-3 form-group">
              <label htmlFor="usr">Almacenamiento(Opcional):</label>
              <input
                value={almacenamiento}
                name="almacenamiento"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>
            <div className="col-12 pt-5 text-center">
              <button
                disabled={loading}
                type="submit"
                className="btn btn-primary"
              >
                Crear Producto
              </button>
            </div>
            {loading ? (
              <div>
                <div className=" justify-content-center loader">
                  <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
                </div>
              </div>
            ) : null}
          </div>
        </form>
      </div>
    </div>
  );
};
