import { types } from "../types/types";

// const state = {
//     name: 'Fernando',
//     logged: true
// }

const data = localStorage.getItem("user");

export const authReducer = (
  state = { logged: data ? JSON.parse(localStorage.getItem("user")) : false },
  action
) => {
  switch (action.type) {
    case types.login:
      return {
        ...action.payload,
        logged: true,
      };

    case types.logout:
      return {
        logged: false,
      };

    default:
      return state;
  }
};
