import React, { useContext } from "react";
import { types } from "../../types/types";
import { useForm } from "../../hooks/useForm";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import Swal from "sweetalert2";
export const LoginScreen = ({ history }) => {
  localStorage.setItem("logged", false);

  const [values, handleInputChange, reset] = useForm({
    layout: "",
    nombre: "",
  });
  const dispatch = useDispatch();
  const { layout, nombre } = values;

  const handleLogin = (e) => {
    e.preventDefault();

    if (layout === "adminmobi@") {
      const lastPath = localStorage.getItem("lastPath") || "/";
      localStorage.setItem("user", true);
      dispatch({
        type: types.login,
        payload: {
          name: nombre.charAt(0).toUpperCase() + nombre.slice(1),
        },
      });
    } else {
      Swal.fire("Error", " Contraseña incorrecta", "error");
      reset();
    }
  };
  return (
    <div className="auth__main">
      <div className="container">
        <div className="row">
          <div className="col-sm-9 col-md-7 col-lg-5 mx-auto">
            <div className="card card-signin my-5">
              <div className="card-body">
                <h5 className="card-title text-center">
                  Administración Mobicell
                </h5>
                <form onSubmit={handleLogin} className="form-signin">
                  <div className="form-label-group">
                    <input
                      name="nombre"
                      onChange={handleInputChange}
                      value={nombre}
                      type="text"
                      id="inputName"
                      className="form-control"
                      placeholder=" Nombre(opcional)"
                    />
                    <label htmlFor="inputName">Nombre(opcional)</label>
                  </div>
                  <div className="form-label-group">
                    <input
                      name="layout"
                      onChange={handleInputChange}
                      value={layout}
                      type="password"
                      id="inputPassword"
                      className="form-control"
                      placeholder="
                      adminmobi@"
                    />
                    <label htmlFor="inputPassword">Contraseña:adminmobi@</label>
                  </div>

                  <button
                    className="btn btn-lg btn-primary btn-block text-uppercase"
                    type="submit"
                  >
                    Iniciar Sesion
                  </button>
                  <hr className="my-4" />
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
