import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router";
import { useForm } from "../../hooks/useForm";
import { pedidoFindByid } from "./PedidoFindById";
import { useHistory } from "react-router-dom";
import { putPedido } from "./api/PutPedido";
import Swal from "sweetalert2";

export const UpdatePedidosScreen = () => {
  const store = useSelector((data) => data.pedidos.data);
  const history = useHistory();
  const { id } = useParams();
  const [dato, setdato] = useState([]);
  const data = !!store && pedidoFindByid(store, id);
  useEffect(() => {
    setValues({ ...data });
  }, [data]);

  const [loading, setloading] = useState(false);
  const [values, handleInputChange, reset, setValues] = useForm({});

  const {
    producto,
    total,
    direccion,
    nombre,
    apellido,
    fechaDeCompra,
    liquidado,
    fechaDepositado,
    fechaEntregado,
    entregado,
    enviado,
    color,
    telefono,
    departamento,
  } = values;
  const handleEnviado = (e) => {
    setValues({ ...values, enviado: true });
  };
  const e = (e) => {
    setValues({ ...values, enviado: false });
  };
  const guardar = async (e) => {
    setloading(true);
    e.preventDefault();

    putPedido(values)
      .then((res) => {
        setloading(false);
        // const { modelo } = res;
        Swal.fire(
          "¡Pedido Modificado!",
          ` Pedido Modificado correctamente`,
          "success"
        );
      })
      .catch((error) => {
        setloading(false);
        Swal.fire("Eror al realizar el pedido", `${error}`, "error");
      });
  };
  const handleEntregado = () => {
    setValues({ ...values, entregado: true });
  };
  const handleNoEntregado = () => {
    setValues({ ...values, entregado: false });
  };
  const handleDepositado = () => {
    setValues({ ...values, liquidado: true });
  };
  const handleNoDepositado = () => {
    setValues({ ...values, liquidado: false });
  };
  return (
    <div className="container-fluid">
      <h1>Actualizar Datos de Pedido</h1>
      <div className="row">
        <form
          encType="multipart/form-data"
          className="col-12"
          onSubmit={guardar}
        >
          <div class="form-row">
            <div className="col-12 col-md-6 form-group">
              <label htmlFor="usr">Nombre de Producto:</label>

              <input
                id="producto"
                value={producto}
                onChange={handleInputChange}
                name="producto"
                type="text"
                className="form-control"
              />
            </div>

            <div className=" col-12 col-md-3 form-group">
              <label htmlFor="usr">Total</label>

              <input
                value={total}
                name="total"
                onChange={handleInputChange}
                type="number"
                className="form-control"
              />
            </div>

            <div className="col-12  col-md-3 form-group">
              <label htmlFor="usr">Direccion</label>
              <input
                name="direccion"
                onChange={handleInputChange}
                value={direccion}
                type="text"
                className="form-control"
              />
            </div>

            <div className="col-12 col-md-3 form-group">
              <label htmlFor="usr">Departamento:</label>
              <input
                value={departamento}
                name="departamento"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>
            <div className="col-6 col-md-3 form-group">
              <label htmlFor="usr">Numero de Telefono:</label>
              <input
                value={telefono}
                name="telefono"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>

            <div className="col-12 col-md-3 form-group">
              <label htmlFor="usr">Fecha de Compra:</label>
              <input
                disabled="true"
                value={fechaDeCompra}
                name="fechaDeCompra"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>
            <div className="col-12 col-md-3 form-group">
              <label htmlFor="usr">Fecha Entregado:</label>
              <input
                value={fechaEntregado}
                name="fechaEntregado"
                onChange={handleInputChange}
                type="text"
                className="form-control"
              />
            </div>
            <div className="col-6 col-md-3 form-group">
              <label>Estado de Deposito:</label>
              <div className="radio">
                <label>
                  <input
                    onClick={handleDepositado}
                    checked={liquidado === true ? "true" : null}
                    type="radio"
                    name="optradio2"
                  />
                  Depositado
                  <i className={liquidado ? " verde fas fa-check" : ""}></i>
                </label>
              </div>
              <div className="radio">
                <label>
                  <input
                    onClick={handleNoDepositado}
                    checked={liquidado === false ? "false" : null}
                    type="radio"
                    name="optradio2"
                  />
                  No Depositado
                  <i className={!liquidado ? "rojo fas fa-times" : ""}></i>
                </label>
              </div>
            </div>
            <div className="col-6 col-md-3 form-group">
              <label>Estado de Entrega:</label>
              <div className="radio">
                <label>
                  <input
                    onClick={handleEntregado}
                    checked={entregado === true ? "true" : null}
                    type="radio"
                    name="optradio3"
                  />
                  Entregado
                  <i className={entregado ? " verde fas fa-check" : ""}></i>
                </label>
              </div>
              <div className="radio">
                <label>
                  <input
                    onClick={handleNoEntregado}
                    checked={entregado === false ? "false" : null}
                    type="radio"
                    name="optradio3"
                  />
                  No Entregado
                  <i className={!entregado ? " rojo fas fa-times" : ""}></i>
                </label>
              </div>
            </div>
            <div className="col-6 col-md-3 form-group">
              <label>Estado de envio:</label>
              <div className="radio">
                <label>
                  <input
                    onClick={handleEnviado}
                    checked={enviado === true ? "true" : null}
                    type="radio"
                    name="optradio"
                  />
                  Enviado
                  <i className={enviado ? " verde fas fa-check" : ""}></i>
                </label>
              </div>
              <div className="radio">
                <label>
                  <input
                    onClick={e}
                    checked={enviado === false ? "false" : null}
                    type="radio"
                    name="optradio"
                  />
                  No Enviado
                  <i className={!enviado ? " rojo fas fa-times" : ""}></i>
                </label>
              </div>
            </div>
            <div className="col-12 py-5 text-center">
              <button
                disabled={loading}
                type="submit"
                className=" btn btn-primary"
              >
                Actualizar Datos
              </button>
            </div>
            {loading ? (
              <div>
                <div className=" justify-content-center loader">
                  <div className="spinner-border" role="status">
                    <span className="sr-only">Loading...</span>
                  </div>
                </div>
              </div>
            ) : null}
          </div>
        </form>
      </div>
    </div>
  );
};
