import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import { findByid } from "./modelosFindById";
import { useForm } from "../../hooks/useForm";
export const IPhoneEditScreen = () => {
  const store = useSelector((data) => data.modelos.data);

  const [setmodelos] = useState([]);
  const { id } = useParams();
  const data = !!store && findByid(store, id);

  useEffect(() => {
    setValues({ ...data });
  }, [data]);
  const [values, handleInputChange, reset, setValues] = useForm({});
  const { Precio, modelo, color, PrecioAnterior, almacenamiento } = values;
  return (
    <div>
      <h1>{modelo}</h1>
      <input value={Precio} />
      <input value={modelo} />
      <input value={color} />
      <input value={PrecioAnterior} />
      <input value={almacenamiento} />
      <button>Editar</button>
    </div>
  );
};
