import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { Switch, Route, Redirect } from "react-router";
import { getModelos } from "../api/api";
import { getPedidos } from "../api/getPedidos";
import { ClientesBorrar } from "../components/ClientesBorrar";
import { ClientesScreen } from "../components/dashboard/ClientesScreen";
import { HomeScreen } from "../components/dashboard/HomeScreen";
import { ModelosScreen } from "../components/dashboard/ModelosScreen";
import { CreateProductScreen } from "../components/ModelosIphone/CreateProductScreen";
import { IPhoneEditScreen } from "../components/ModelosIphone/IPhoneEditScreen";
import { Navbar } from "../components/navbar/Navbar";
import { TopBar } from "../components/navbar/TopBar";
import { PedidosScreen } from "../components/pedidos/PedidosScreen";
import { UpdatePedidosScreen } from "../components/pedidos/UpdatePedidosScreen";
export const DashRouter = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    (async () => {
      const datas = await getModelos();
      dispatch({ type: "guardar-modelo", payload: { data: datas } });
    })();
  }, []);
  useEffect(() => {
    (async () => {
      const datas = await getPedidos();

      dispatch({ type: "guardar-pedidos", payload: { data: datas } });
    })();
  }, []);
  useEffect(() => {
    /* eslint-disable */

    $("#sidebarToggle, #sidebarToggleTop").on("click", function (e) {
      $("body").toggleClass("sidebar-toggled"),
        $(".sidebar").toggleClass("toggled"),
        $(".sidebar").hasClass("toggled") &&
          $(".sidebar .collapse").collapse("hide");
    }),
      $(window).resize(function () {
        $(window).width() < 768 && $(".sidebar .collapse").collapse("hide"),
          $(window).width() < 480 &&
            !$(".sidebar").hasClass("toggled") &&
            ($("body").addClass("sidebar-toggled"),
            $(".sidebar").addClass("toggled"),
            $(".sidebar .collapse").collapse("hide"));
      }),
      $("body.fixed-nav .sidebar").on(
        "mousewheel DOMMouseScroll wheel",
        function (e) {
          if (768 < $(window).width()) {
            var o = e.originalEvent,
              l = o.wheelDelta || -o.detail;
            (this.scrollTop += 30 * (l < 0 ? 1 : -1)), e.preventDefault();
          }
        }
      ),
      $(document).on("click", "a.scroll-to-top", function (e) {
        var o = $(this);
        $("html, body")
          .stop()
          .animate(
            { scrollTop: $(o.attr("href")).offset().top },
            1e3,
            "easeInOutExpo"
          ),
          e.preventDefault();
      });
  }, []);
  return (
    <div id="page-top">
      <div id="wrapper">
        <Navbar />

        <div id="content-wrapper" className="d-flex flex-column">
          {/* Main Content */}
          <div id="content">
            <TopBar />
            <div className="container-fluid">
              <div className="row">
                <Switch>
                  <Route exact path="/" component={HomeScreen} />
                  <Route path="/usuarios" component={HomeScreen} />
                  <Route path="/modelos" component={ModelosScreen} />
                  <Route path="/modelo/:id" component={IPhoneEditScreen} />
                  <Route path="/pedido/:id" component={UpdatePedidosScreen} />
                  <Route
                    path="/nuevo-producto"
                    component={CreateProductScreen}
                  />
                  <Route path="/pedidos" component={PedidosScreen} />

                  <Redirect to="/" />
                </Switch>
              </div>
            </div>

            {/* /.container-fluid */}
          </div>
          {/* End of Main Content */}
        </div>
      </div>
    </div>
  );
};
