import React, { useEffect, useState } from "react";
import { getModelos } from "../../api/api";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { deleteProducto } from "../../api/DeleteProducto";
import Swal from "sweetalert2";
import DataTable from "../../DataTable/index";
import { useHistory } from "react-router-dom";
import { getPedidos } from "../../api/getPedidos";
import TablaPedidos from "../../DataTable/TablaPedidos";

export const PedidosScreen = () => {
  const history = useHistory();
  const headers = [
    { name: "Nombre", field: "nombre", sortable: true },
    { name: "Apellido", field: "apellido", sortable: true },
    { name: "Total", field: "cantidad", sortable: false },
    { name: "Producto", field: "producto", sortable: false },
    { name: "Enviado", field: "enviado", sortable: false },
    { name: "Entregado", field: "entregado", sortable: false },
    { name: "Liquidado", field: "liquidado", sortable: false },
  ];
  const [dataFetch, setdataFetch] = useState([]);
  const todo = useSelector((state) => state.modelos.data);
  const [estadoReduxStore, setestadoReduxStore] = useState([]);

  const dispatch = useDispatch();
  const [loading, setloading] = useState(false);
  const actualizar = (id) => {
    history.push(`/pedido/${id}`);
  };
  useEffect(() => {
    (async () => {
      const datas = await getPedidos();
      console.log(datas);
      setdataFetch(datas);

      dispatch({ type: "guardar-pedidos", payload: { data: datas } });
      setestadoReduxStore(todo);
    })();
  }, []);
  const eliminar = (id) => {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        (async () => {
          setloading(true);
          await deleteProducto({ id: id });
          const dato = await dataFetch.filter((data) => data._id != id);
          setdataFetch(dato);
          dispatch({ type: "actualizar-pedidos", payload: { data: dato } });
          setloading(false);
          Swal.fire("Deleted!", "Your file has been deleted.", "success");
        })();
      }
    });
  };
  return (
    <>
      <TablaPedidos
        titulos={headers}
        informacion={dataFetch}
        borrar={eliminar}
        actualizar={actualizar}
        loading={loading}
      />
    </>
  );
};
