import React, { useEffect, useState, useMemo } from "react";
import Header from "../components/DataTable/Header";
import { TableHeader, Pagination, Search } from "../components/DataTable";
import useFullPageLoader from "../hooks/useFullPageLoader";
import { Modal } from "../components/modal/Modal";

const TablaPedidos = ({
  informacion,
  titulos,
  borrar,
  actualizar,
  loading,
}) => {
  const [comments, setComments] = useState([]);
  const [loader, showLoader, hideLoader] = useFullPageLoader();
  const [totalItems, setTotalItems] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [search, setSearch] = useState("");
  const [sorting, setSorting] = useState({ field: "", order: "" });
  const ITEMS_PER_PAGE = 10;
  let i = 0;
  const [contador, setcontador] = useState(0);
  const headers = [
    { name: "No#", field: "id", sortable: false },
    { name: "Name", field: "name", sortable: true },
    { name: "Email", field: "email", sortable: true },
    { name: "Comment", field: "body", sortable: false },
  ];
  console.log(informacion);
  const commentsData = useMemo(() => {
    let computedComments = informacion;

    if (search) {
      computedComments = computedComments.filter(
        (informacion) =>
          informacion.nombre.toLowerCase().includes(search.toLowerCase()) ||
          informacion.apellido.toLowerCase().includes(search.toLowerCase()) ||
          informacion.direccion.toLowerCase().includes(search.toLowerCase()) ||
          informacion.departamento.toLowerCase().includes(search.toLowerCase())
      );
    }

    setTotalItems(computedComments.length);

    //Sorting comments
    if (sorting.field) {
      const reversed = sorting.order === "asc" ? 1 : -1;
      computedComments = computedComments.sort(
        (a, b) => reversed * a[sorting.field].localeCompare(b[sorting.field])
      );
    }

    //Current Page slice
    return computedComments.slice(
      (currentPage - 1) * ITEMS_PER_PAGE,
      (currentPage - 1) * ITEMS_PER_PAGE + ITEMS_PER_PAGE
    );
  }, [informacion, currentPage, search, sorting]);

  return (
    <>
      <div style={{ position: "relative" }} className="row w-100 ">
        <div className="col mb-3 text-center">
          <div className="row">
            <div className=" py-3 d-flex flex-row-reverse ml-auto ">
              <Search
                onSearch={(value) => {
                  setSearch(value);
                  setCurrentPage(1);
                }}
              />
            </div>
          </div>
          <div className="table-responsive ml-2 ">
            <table
              style={{ padding: 0, margin: 0 }}
              className="table  table-striped  "
            >
              <TableHeader
                headers={titulos}
                onSorting={(field, order) => setSorting({ field, order })}
              />
              <tbody>
                {commentsData.map(
                  ({
                    nombre,
                    _id,
                    apellido,
                    telefono,
                    departamento,
                    direccion,
                    producto,
                    cantidad,
                    enviado,
                    entregado,
                    liquidado,
                  }) => (
                    <tr key={_id} className="mt-2">
                      <td>{nombre}</td>
                      <td>{apellido}</td>
                      <td>{cantidad}</td>
                      <td>{producto}</td>

                      <td>
                        <i
                          className={
                            enviado
                              ? " verde fas fa-check"
                              : "rojo fas fa-times"
                          }
                        ></i>
                      </td>
                      <td>
                        <i
                          className={
                            entregado
                              ? " verde fas fa-check"
                              : "rojo fas fa-times"
                          }
                        ></i>
                      </td>
                      <td>
                        <i
                          class={
                            liquidado
                              ? " verde fas fa-check"
                              : "rojo fas fa-times"
                          }
                        ></i>
                      </td>

                      <td>
                        <td
                          className="float "
                          style={{
                            textDecoration: "none",
                            position: "relative",
                          }}
                        >
                          <Modal />
                        </td>
                        <td
                          className="float my-float"
                          style={{ position: "relative" }}
                        >
                          <button
                            disabled={loading}
                            onClick={() => {
                              borrar(_id);
                            }}
                            className="  btn btn-danger"
                          >
                            <i className="fas fa-trash"></i>
                          </button>
                        </td>
                        <td
                          className="float my-float"
                          style={{ position: "relative" }}
                        >
                          <button
                            disabled={loading}
                            onClick={() => {
                              actualizar(_id);
                            }}
                            className="btn btn-success"
                          >
                            <i className="fas fa-edit"></i>
                          </button>
                        </td>
                      </td>
                    </tr>
                  )
                )}
              </tbody>
            </table>
          </div>

          <div className="col-md-12 ">
            <Pagination
              total={totalItems}
              itemsPerPage={ITEMS_PER_PAGE}
              currentPage={currentPage}
              onPageChange={(page) => setCurrentPage(page)}
            />
          </div>
        </div>
        {loading || informacion.length < 1 ? (
          <div>
            <div className=" justify-content-center loader">
              <div className="spinner-border" role="status">
                <span className="sr-only">Loading...</span>
              </div>
            </div>
          </div>
        ) : null}
      </div>
    </>
  );
};

export default TablaPedidos;
