import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { Link } from "react-router-dom";
import { getPedidos } from "../../api/getPedidos";
import { types } from "../../types/types";
import { filtrarPedidos } from "./actions/FiltrarPedidos";
export const TopBar = () => {
  const [estadoNotificaciones, setestadoNotificaciones] = useState("");
  const stateEnvios = useSelector((state) => state.pedidos);
  const [dataEnvios, setdataEnvios] = useState([]);

  useEffect(() => {
    (async () => {
      const datas = await getPedidos();
      const filtrado = filtrarPedidos(datas);
      console.log(filtrado);
      setdataEnvios(filtrado);
      let dato = filtrado.length;
      setestadoNotificaciones(dato);

      dispatch({ type: "guardar", payload: { data: datas } });
    })();
  }, []);
  const dispatch = useDispatch();
  const history = useHistory();
  const handleLogout = () => {
    history.replace("/login");
    JSON.stringify(localStorage.setItem("user", false));
    dispatch({
      type: types.logout,
    });
  };
  return (
    <nav className="navbar navbar-expand navbar-light bg-white topbar mb-4 static-top shadow">
      {/* Sidebar Toggle (Topbar) */}
      <button
        id="sidebarToggleTop"
        className="btn btn-link d-md-none rounded-circle mr-3"
      >
        <i className="fa fa-bars" />
      </button>
      {/* Topbar Search */}

      {/* Topbar Navbar */}
      <ul className="navbar-nav ml-auto">
        {/* Nav Item - Search Dropdown (Visible Only XS) */}
        <li className="nav-item dropdown no-arrow d-sm-none">
          {/* Dropdown - Messages */}
          <div
            className="dropdown-menu dropdown-menu-right p-3 shadow animated--grow-in"
            aria-labelledby="searchDropdown"
          ></div>
        </li>
        {/* Nav Item - Alerts */}
        <li className="nav-item dropdown no-arrow mx-1">
          <a
            className="nav-link dropdown-toggle"
            href="#"
            id="alertsDropdown"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <i className="fas fa-bell fa-fw" />
            {/* Counter - Alerts */}
            <span className="badge badge-danger badge-counter">
              {estadoNotificaciones}
            </span>
          </a>
          {/* Dropdown - Alerts */}
          <div
            className="dropdown-list dropdown-menu dropdown-menu-right shadow animated--grow-in"
            aria-labelledby="alertsDropdown"
          >
            <h6 className="dropdown-header">Alerts Center</h6>
            {dataEnvios.map(
              ({ _id, fechaDeCompra, nombre, producto, apellido }) => (
                <Link
                  className="dropdown-item d-flex align-items-center"
                  to={`/pedido/${_id}`}
                >
                  <div className="mr-3">
                    <div className="icon-circle bg-success">
                      <i class="fas fa-shipping-fast"></i>
                    </div>
                  </div>
                  <div>
                    <div className="small text-gray-500">{fechaDeCompra}</div>
                    <span className="font-weight-bold">Nuevo Pedido</span>
                    <div>
                      <p>
                        Nombre: {nombre}
                        <p>Producto: {producto}</p>
                      </p>
                    </div>
                  </div>
                </Link>
              )
            )}
          </div>
        </li>
        {/* Nav Item - Messages */}

        <div className="topbar-divider d-none d-sm-block" />
        {/* Nav Item - User Information */}
        <li className="nav-item dropdown no-arrow">
          <a
            className="nav-link dropdown-toggle"
            href="#"
            id="userDropdown"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <span className="mr-2 d-none d-lg-inline text-gray-600 small">
              Luis Reyes
            </span>
            <i class="fas fa-user"></i>
          </a>
          {/* Dropdown - User Information */}
          <div
            className="dropdown-menu dropdown-menu-right shadow animated--grow-in"
            aria-labelledby="userDropdown"
          >
            <a className="dropdown-item" href="#">
              <i className="fas fa-user fa-sm fa-fw mr-2 text-gray-400" />
              Profile
            </a>
            <a className="dropdown-item" href="#">
              <i className="fas fa-cogs fa-sm fa-fw mr-2 text-gray-400" />
              Settings
            </a>
            <a className="dropdown-item" href="#">
              <i className="fas fa-list fa-sm fa-fw mr-2 text-gray-400" />
              Activity Log
            </a>
            <div className="dropdown-divider" />
            <button
              onClick={handleLogout}
              className="dropdown-item"
              data-toggle="modal"
              data-target="#logoutModal"
            >
              <i className="fas fa-sign-out-alt fa-sm fa-fw mr-2 text-gray-400" />
              Logout
            </button>
          </div>
        </li>
      </ul>
    </nav>
  );
};
